.saved-list{
  background-color: aliceblue;
  width: 100%;
  font-size: 3.5vw;
}

.saved-movie{
  margin-left: 10px;
  margin-top: 10px;
  width: 90%;
  padding: 4px 0;
  background-color: rgb(227, 228, 240);
  border-radius: 5px;
}
.saved-movies-card{
  background-color:white;
  width: 34vw;
  margin: 5px auto;
  border: 1px black solid;
  
}
.saved-movies-card button{
  margin: 10px;
  background-color: blue;
  color: white;
  border: none;
  outline: none;
  border-radius: 5px;
  cursor: pointer;
}

@media screen and (max-width: 991px) and (min-width: 320px) {
  .saved-movies-card{
      width: 54vw;
  }
  .saved-list{
      font-size: 7vw;
  }
}